.hero-container {
  max-width: 100vw;
  height: 75vh;
  top: 0;
  left: 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('https://res.cloudinary.com/contentexs/image/upload/v1580121612/hero-bkg.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;

  .hero-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba($clr2, 0.7);
    border-radius: 5px;
    width: 55vw;
    padding-bottom: 30px;

    .text {
      margin-bottom: 30px;
      color: $clr3;

      & > span:first-child {
        font-family: righteous;
        color: $clr8;
        font-size: 5rem;
        letter-spacing: 3px;
        word-spacing: 5px;
      }

      & > span:last-child {
        font-family: raleway-bold;
        font-size: 1.2rem;
        text-transform: uppercase;
        letter-spacing: 2px;
        word-spacing: 4px;
      }
    }

    .how-to {
      margin-bottom: 30px;
      width: 100%;

      & > ul > li {
        margin-right: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        & > img {
          width: 50px;
          height: auto;
          object-fit: contain;
        }

        & > img:hover {
          filter: invert(0.5) sepia(36) saturate(390) hue-rotate(10deg);
        }

        & > span {
          font-family: raleway-bold;
          font-size: 1.2rem;
          letter-spacing: 2px;
          word-spacing: 3px;

          span {
            font-size: 1.3rem;
          }
        }
      }
    }

    .search-container {
      width: 80%;
    }
  }
}

.section-title-container {
  margin: rem(50px) rem(50px) 0;

  h2 {
    margin-bottom: 20px;
    font-family: righteous;
    font-size: 2rem;
    letter-spacing: 3px;
  }
}
